import { useState } from "react"
import { Check, ChevronDown, ChevronUp } from "lucide-react"
import { Popover, PopoverContent, PopoverTrigger } from "./ui/popover"
import { Button } from "./ui/button"
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "./ui/command"
import { cn } from "../lib/utils"
import { PopulationData } from "../lib/parsePopulations"
import { useMediaQuery } from "../hooks/useMediaQuery"

interface Props {
  populationData: PopulationData
}

export const BetterDropdown = ({ populationData }: Props) => {
  const [open, setOpen] = useState(false)
  const [search, setSearch] = useState("")
  const [location, setLocation] = useState("")
  const Icon = open ? ChevronUp : ChevronDown
  const isDesktop = useMediaQuery("(min-width: 768px)")

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className="w-full sm:w-[400px] justify-between"
        >
          {location || "Select city..."}
          <Icon className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-full sm:w-[400px] p-0" forceMount>
        <Command
          filter={(value, search) => {
            if (value.includes(search.toLocaleLowerCase())) return 1
            return 0
          }}
        >
          <CommandInput
            value={search}
            onValueChange={setSearch}
            placeholder="Search by city..."
          />
          <CommandEmpty>City not found </CommandEmpty>
          <CommandGroup className="max-h-[300px] overflow-y-scroll">
            {populationData.map((place) => {
              const foundIndex = place.location
                .toLocaleLowerCase()
                .indexOf(search.toLocaleLowerCase(), 0)
              const text =
                foundIndex > -1 ? (
                  <span>
                    {place.location.substring(0, foundIndex)}
                    {
                      <span className="font-bold">
                        {place.location.substring(
                          foundIndex,
                          foundIndex + search.length
                        )}
                      </span>
                    }
                    {place.location.substring(foundIndex + search.length)}
                  </span>
                ) : (
                  place.location
                )
              return (
                <CommandItem
                  key={place.location}
                  value={place.location}
                  onSelect={(currentLocation) => {
                    setLocation(
                      currentLocation === location.toLocaleLowerCase()
                        ? ""
                        : place.location
                    )
                    setOpen(false)
                  }}
                >
                  <Check
                    className={cn(
                      "mr-2 h-4 w-4",
                      location === place.location ? "opacity-100" : "opacity-0"
                    )}
                  />
                  <span>{text}</span>
                </CommandItem>
              )
            })}
          </CommandGroup>
        </Command>
      </PopoverContent>
    </Popover>
  )
}
